// USER VARIABLES SECTION

$accent: #ffba00 // Accent Color
$fontsize: 16px // Body Font Size
$textcolor: #000 // Text Color
$lineheight: 1.65 // Document Line Height
$userfont: gothampro, sans-serif // User Font Family
$systemfont: -apple-system, BlinkMacSystemFont, Arial, sans-serif // System Font Family

// Bootstrap compatible gutter variable => $gutter
$grid-gutter-width: 30px
$gutter: $grid-gutter-width

// IMPORT SECTION

@import 'node_modules/bootstrap/scss/bootstrap-reboot.scss' // Bootstrap Reboot collection
@import 'node_modules/bootstrap/scss/mixins/_breakpoints.scss' // Bootstrap Breakpoints mixin
@import 'node_modules/bootstrap/scss/bootstrap-grid.scss' // Optional Bootstrap Grid
// @import 'node_modules/bootstrap/scss/_utilities.scss' // Optional Bootstrap Utilites

// FONTS LOAD SECTION

@mixin font($fname, $fstyle, $fweight, $furl)
	@font-face
		font-family: $fname
		font-style: $fstyle
		font-weight: $fweight
		src: url($furl) format('woff2')

@include font('gothampro', normal, 400, '../fonts/GothamPro.woff2')
@include font('gothampro', normal, 700, '../fonts/GothamPro-Bold.woff2')
@include font('gothampro', normal, 900, '../fonts/GothamPro-Black.woff2')
@include font('hanson', normal, 700, '../fonts/Hanson-Bold.woff2')

// GENERAL CSS SETTINGS

::placeholder
	color: #666

::selection
	background-color: $accent
	color: #fff

input, textarea
	outline: none
	&:focus:required:invalid
		border-color: red
	&:required:valid
		border-color: green

a, button
	outline: none !important
	text-decoration: none

section
	position: relative

body
	font-family: $userfont
	font-size: $fontsize
	line-height: $lineheight
	color: $textcolor
	min-width: 320px
	position: relative
	overflow-x: hidden
	padding-top: 100px

.img-fluid
	max-width: 100%
	height: auto

h1, .h1
	font-size: 64px
	letter-spacing: 0.005em
	font-weight: 700
	color: #151515

h2, .h2
	font-size: 64px
	letter-spacing: 0.005em
	font-weight: 900
	color: #151515
	text-align: center

.button
	background-color: $accent
	border-radius: 20px
	padding: 20.5px 44px
	border: 3px solid $accent
	display: inline-flex
	justify-content: center
	align-items: center
	color: #fff
	font-size: 20px
	font-weight: 700
	transition: all 0.25s ease
	&:focus
		color: #fff
		background-color: $accent
	&:hover
		color: #000
		background-color: #fff
		

/* Button top */
.btn-top
	position: fixed
	display: flex
	align-items: center
	justify-content: center
	bottom: 30px
	background-color: #ededed
	box-shadow: 0px 4px 10px rgba(138, 138, 138, 0.4)
	border-radius: 12px
	color: #fff
	font-size: 26px
	width: 50px
	height: 50px
	right: -100px
	z-index: 15
	cursor: pointer
	text-align: center
	transition: all .25s ease
	// .fa
	// 	line-height: 0
	&.active
		right: 30px
	&:hover
		background-color: $accent
		opacity: 1

// notice
.cookie-notice
	position: fixed
	display: flex
	justify-content: center
	align-items: center
	padding: 1rem 0
	bottom: 0
	width: 100%
	background: #191919
	color: #F6F6F6
	font-size: .9rem
	z-index: 999
	p
		margin-bottom: 0
	a
		color: #F6F6F6
		&:hover
			text-decoration: none
		&:visited
			color: #F6F6F6
	.cookie
		font-size: 2.2rem
		margin-right: 1rem
		margin-left: 1rem
		// animation: spin 2s linear infinite
	.accept-cookies
		cursor: pointer
		border: none
		outline: none
		background: #F6F6F6
		color: #191919
		font-weight: 700
		padding: .5rem 1.5rem
		margin-right: 1rem
		margin-left: 3rem
		transition: .35s ease
		&:hover
			box-shadow: .01rem .01rem .1rem #DDDDDD

