/** Slider fleet */
.ej-2 {
  padding-top: 100px;
  padding-bottom: 80px;
  .h2 {
    margin-bottom: 55px;
  }
  &__inner {
  }
}

.slider-fleet {
  position: relative;
	padding: 30px 0 !important;
  .swiper-slide {
		.slider-fleet__item{
			transform: scale(0.7);
		}
    &.swiper-slide-active {
			.slider-fleet__item{
				transform: scale(1);
			}
			.slider-fleet__item-img{
				background-color: $accent;
			}
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
		text-align: center;
		box-shadow: 0px 4px 10px rgba(138, 138, 138, 0.4);
		border-radius: 40px;
		transition: all .5s ease-in-out;
		&-img{
			background-color: #fafafa;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 4px 4px rgba(0, 0, 0, 0.25);
			border-radius: 40px;
			padding: 40px 20px;
			transition: all .5s ease-in-out;
		}
		&-head{
			font-size: 50px;
			font-weight: 700;
			line-height: 1.4;
			padding: 30px 0;
		}
  }
	.swiper-button-prev:after, .swiper-rtl .swiper-button-next:after,
	.swiper-button-next:after, .swiper-rtl .swiper-button-prev:after{
		display: none;
	}
	.swiper-button-prev, .swiper-rtl .swiper-button-next{
		left: 0;
	}
	.swiper-button-next, .swiper-rtl .swiper-button-prev{
		right: 0;
	}
	.swiper-button-prev,
	.swiper-button-next{
		outline: none !important;
		width: 40px;
		height: 40px;
		border-radius: 50%;
		background-color: $accent;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			zoom: .7;
			&.svg-left{
				margin-left: -5px;
			}
			&.svg-right{
				margin-right: -5px;
			}
		}
	}
}
