/** Header */
.home-header {
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.15);
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;
  background-color: #fff;
  opacity: 0;
  transform: translateY(-100%);
  transition: all 0.25s ease-in;
  &.is-show {
    opacity: 1;
    transform: translateY(0);
  }
  &__nav {
    margin-right: 70px;
    &-menu {
      list-style-type: none;
      padding: 0;
      margin: 0;
      display: flex;
      align-items: center;
      li {
        margin: 0 15px;
        a {
          padding: 6px;
          font-size: 20px;
          text-decoration: none;
          color: #000;
          transition: color 0.25s ease;
          &:focus,
          &:hover {
            color: $accent;
          }
        }
      }
    }
  }
  &__social {
    display: flex;
    align-items: center;
    &-icon {
      svg path {
        fill: #000;
        transition: fill 0.25s ease;
      }
      &:hover {
        svg path {
          fill: $accent;
        }
      }
    }
    a {
      display: flex;
      width: 40px;
      height: 40px;
      align-items: center;
      margin-right: 40px;
      &:last-of-type {
        margin-right: 0;
      }
    }
  }
}
.top-line {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-wrap {
      display: flex;
      align-items: center;
    }
  }
}
.logo {
  &__images {
  }
}

/** Main banner */
.main-banner {
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: right right;
  min-height: calc(100vh - 100px);
  min-height: 100vh;
  padding-top: 200px;
  padding-bottom: 200px;
  margin-bottom: 60px;
  opacity: 0;
  background-image: url(../images/dist/banner-bg.jpg);
  transition: all 0.35s ease-in;
  &::before {
    content: "";
    position: absolute;
    right: 0;
    top: 23%;
    background-image: url(../images/dist/main-car.png);
    background-repeat: no-repeat;
    z-index: 2;
    width: 1165px;
    height: 639px;
    zoom: 0.87;
    opacity: 0;
    transform: translateX(100%);
    transition: all 0.3s ease-in;
  }
  &.is-show {
    opacity: 1;
    &::before {
      opacity: 1;
      transform: translateX(0);
    }
    .main-banner__content {
      opacity: 1;
      transform: translateY(0);
    }
  }
  &__inner {
    position: relative;
    z-index: 5;
  }
  &__content {
    opacity: 0;
    transform: translateY(-50%);
    transition: all 0.3s ease-in;
    .h1 {
      font-family: hanson;
      position: relative;
      &::before {
        content: "";
        background-color: $accent;
        width: 93%;
        height: 9px;
        position: absolute;
        top: 45px;
        left: 0;
        z-index: -1;
      }
    }
    .under-h1 {
      display: block;
      margin-top: 20px;
      color: #797b8a;
    }
  }
  &__btn {
    margin-top: 50px;
    .button {
      min-width: 480px;
    }
  }
}

/** ej-1 */
.ej-1 {
  padding-top: 90px;
  padding-bottom: 80px;
  .h2 {
    margin-bottom: 90px;
  }
  &__inner {
    width: 100%;
    background-image: url(../images/dist/decor-line.svg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }
  &__items {
    display: grid;
    grid-column: span 3;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px 40px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    text-align: center;
    &-icon {
      width: 150px;
      height: 150px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #ededed;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 15px;
      margin-right: auto;
      margin-left: auto;
      margin-bottom: 40px;
      filter: drop-shadow(0px 4px 10px rgba(138, 138, 138, 0));
      transition: all 0.25s ease;
      &:hover {
        background-color: $accent;
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0);
        filter: drop-shadow(0px 4px 10px rgba(138, 138, 138, 0.4));
      }
    }
    &-head {
      margin-bottom: 10px;
      font-size: 36px;
      font-weight: 700;
      line-height: 1.4;
    }
    &-text {
      font-size: 24px;
      line-height: 1.4;
    }
  }
}

/** CONTACT */
.ej-contact {
  padding-top: 90px;
  .h2 {
    margin-bottom: 110px;
  }
  &__inner {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
    padding-bottom: 137px;
  }
  &__box {
    background-color: #fff;
    box-shadow: 0px 4px 10px rgba(138, 138, 138, 0.4);
    border-radius: 20px;
    padding: 50px;
    max-width: 648px;
  }
  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    &:last-of-type {
      margin-bottom: 0;
    }
    &-icon {
      margin-right: 50px;
      min-width: 60px;
    }
    &-content {
      &--title {
        display: flex;
        align-items: center;
        font-size: 36px;
        font-weight: 700;
      }
      &--descr {
        font-size: 24px;
        a {
          color: #000;
          font-size: 24px;
          text-decoration: underline;
        }
      }
    }
    .home-header__social {
      margin-left: 30px;
      a {
        margin-right: 30px;
        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }
}
.marker-wrapper {
  display: flex;
  justify-content: center;
  margin-top: 30%;
  margin-left: 30%;
}
.marker {
  position: relative;
  display: flex;
  align-items: center;
}
.grow {
  // position:absolute;
  //background:rgba(255,255,255,0.3);
  background: #fff;
  height: 10px;
  width: 10px;
  position: relative;
  left: -60px;
  z-index: 5;
  border-radius: 50%;
  border: 20px solid $accent;

  -webkit-animation: grow 2s ease-in infinite;
  -webkit-animation-delay: 1.2s;
}
.dot {
  // position: absolute;
  height: 80px;
  width: 80px;
  top: -40px;
  left: -40px;
  z-index: 2;
  opacity: 0;
  border: 10px solid lighten($accent, 30);
  background: $accent;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  -webkit-animation: pulse 2s ease-out;
  -moz-animation: pulse 2s ease-out;
  animation: pulse 2s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-delay: 1.2s;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(2.5);
    opacity: 0;
  }
}
@-webkit-keyframes grow {
  0% {
    -webkit-transform: scale(1);
    opacity: 0.5;
  }
  55% {
    -webkit-transform: scale(1.2);
    opacity: 0.65;
  }
  65% {
    -webkit-transform: scale(0.6);
    opacity: 0.75;
  }
  70% {
    -webkit-transform: scale(1);
    opacity: 0.85;
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0.9;
  }
}

/** FOOTER */
.ej-footer {
  padding: 29px 0;
  &__line {
    padding: 20px 0;
    > * {
      min-height: 124px;
    }
  }
}

.legacy-nav {
  display: flex;
  align-items: center;
  justify-content: center;
  a {
    margin-right: 50px;
    font-size: 24px;
    color: #000;
    transition: all 0.25s ease;
    &:hover {
      color: $accent;
    }
    &:last-of-type {
      margin-right: 0;
    }
  }
}
.foot-social-wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  > strong {
    font-weight: 700;
    font-size: 24px;
    margin-right: 20px;
    color: #1d1d1d;
  }
}
.foot-social {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  li {
    list-style-type: none;
    margin: 0 7px;
    a {
      width: 40px;
      height: 40px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #ededed;
      box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
      border-radius: 6px;
      transition: all 0.25s ease;
      &:hover {
        background-color: $accent;
      }
    }
  }
}

.section-primary {
  padding-top: 50px;
  padding-bottom: 50px;
  .h1,
  h1 {
    font-size: 2.4rem;
    line-height: 3.75rem;
  }
  .h2,
  h2 {
    font-size: 1.9rem;
    line-height: 3rem;
    text-align: left;
  }
  .h3,
  h3 {
    font-size: 1.65rem;
    line-height: 2.625rem;
    font-weight: 700;
  }
}

/**Scroll down*/
.scrollDown {
  position: absolute;
  left: 50%;
  bottom: 10px;
  transform: translateX(-50%);
  z-index: 2;
  &__box {
    left: 50%;
    transform: translateY(0%) rotate(45deg);
    opacity: 0;
    &.arr-1 {
      width: 2em;
      height: 2em;
      background-color: transparent;
      z-index: 80;
      bottom: 25px;
      position: absolute;
      border-width: 0 0.25em 0.25em 0;
      border-style: solid;
      border-color: $accent;
      animation: scrolldown 1.2s ease-in-out infinite 0.15s;
    }
    &.arr-2 {
      width: 2em;
      height: 2em;
      background-color: transparent;
      z-index: 80;
      bottom: 40px;
      position: absolute;
      border-width: 0 0.25em 0.25em 0;
      border-style: solid;
      border-color: $accent;
      animation: scrolldown 1.2s ease-in-out infinite;
    }
  }
}

@keyframes scrolldown {
  0% {
    transform: translateY(20%) rotate(45deg);
    opacity: 0.7;
  }
  50% {
    transform: translateY(0%) rotate(45deg);
    opacity: 0.2;
  }
  100% {
    transform: translateY(20%) rotate(45deg);
    opacity: 0.7;
  }
}
