@media (max-width: 1820px) {
  .main-banner:before {
    zoom: 0.8;
  }
}
@media (max-width: 1680px) {
  .main-banner:before {
    zoom: 0.7;
  }
	.main-banner{
		padding-bottom: 200px;
		padding-top: 200px;
	}
}
@media (max-width: 1440px) {
  .main-banner:before {
    zoom: 0.65;
  }
	.main-banner{
		padding-bottom: 150px;
		padding-top: 150px;
	}
}
// X-Large devices (large desktops, less than 1400px)
@media (max-width: 1399.98px) {
  h1,
  .h1 {
    font-size: 55px;
  }
  .main-banner__btn .button {
    min-width: 400px;
  }
  .main-banner:before {
    zoom: 0.6;
  }

	.main-banner{
		padding-bottom: 120px;
		padding-top: 120px;
		min-height: unset;
	}
}

// Large devices (desktops, less than 1200px)
@media (max-width: 1199.98px) {

	.main-banner{
		padding-bottom: 150px;
		padding-top: 100px;
	}
  .h1,
  h1 {
    font-size: 42px;
  }
  .main-banner {
    background-size: 115% 100%;
  }
  .main-banner:before {
    zoom: 0.5;
  }
  .main-banner__content .h1:before {
    top: 30px;
    width: 86%;
  }
  .main-banner__btn .button {
    min-width: 320px;
  }
  .ej-contact__box {
    max-width: 100%;
  }
  .marker-wrapper {
    margin-left: 0;
    margin-top: 0;
    margin-bottom: 110px;
  }

  .ej-footer {
    padding-top: 0;
		padding-bottom: 55px;
  }
  h2,
  .h2 {
    font-size: 54px;
  }
  .ej-1__item-head {
    font-size: 30px;
  }
  .ej-1__item-text {
    font-size: 18px;
  }
  .ej-1__item-icon {
    width: 130px;
    height: 130px;
  }
  .ej-footer__line > * {
    min-height: 60px;
  }
	.ej-footer .logo img{
		max-height: 60px;
	}
	.legacy-nav a, .foot-social-wrap>strong{
		font-size: 18px;
	}
	.btn-top{
		bottom: 15px;
	}
	.btn-top.active{
		right: 15px;
	}
	.main-banner{
		margin-bottom: 0;
	}
	.ej-2{
		padding-bottom: 0;
		padding-top: 30px;
	}
}

// Medium devices (tablets, less than 992px)
@media (max-width: 991.98px) {
	.scrollDown{
		display: none !important;
	}
  .slider-fleet .swiper-slide.swiper-slide-active .slider-fleet__item {
    max-width: 85%;
    margin-left: auto;
    margin-right: auto;
  }
  .main-banner {
    min-height: auto;
    padding-top: 10%;
  }
  .main-banner:before {
    top: 55%;
  }
  .h1,
  h1 {
    font-size: 47px;
  }
  .main-banner__btn {
    margin-top: 30px;
  }
  .ej-1__items {
    grid-column: span 2;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 45px;
  }
  .ej-1__inner {
    background-image: url(../images/dist/decor-line-2.svg);
    background-position: 100% -71px;
  }
  .ej-1__item-icon {
    margin-bottom: 20px;
  }
  .ej-1 {
    padding-bottom: 80px;
		padding-top: 130px;
  }
	.ej-footer__line{
		display: flex;
		padding: 30px 0;
		&.c-gray{
			background-color: #f3f3f3;
			margin-left: -15px;
			margin-right: -15px;
			justify-content: center;
		}
	}
	.ej-contact__inner{
		padding-bottom: 0;
	}
	.ej-contact__item-content--title{
		font-size: 28px;
	}
	.ej-contact__item-content--descr a,
	.ej-contact__item-content--descr{
		font-size: 18px;
	}
	.ej-contact__item .home-header__social{
		zoom: .75;
	}
	.ej-footer{
		padding-bottom: 0;
	}
	.ej-footer__line.c-gray {
		margin-top: 40px;
	}
	.slider-fleet__item-head{
		font-size: 30px;
	}
}

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
	.home-header__nav{
		display: none;
	}
	.legacy-nav a{
		margin-right: 20px;
	}
	.ej-footer__line.c-gray {
		padding: 15px 0;
	}
	.ej-footer{
		padding-bottom: 40px;
	}
	.ej-contact__item-icon{
		margin-right: 30px;
		zoom: .6;
	}
	.ej-contact__box{
		padding: 30px 20px;
	}
	.ej-contact__item-content--title{
		font-size: 22px;
	}
	.ej-contact__item-content--descr, .ej-contact__item-content--descr a{
		font-size: 14px;
	}
	.ej-1__item-icon{
		width: 80px;
		height: 80px;
		background-color: $accent;
		box-shadow: 0px 4px 10px rgba(138, 138, 138, 0.4);
		img{
			zoom: .5;
		}
	}
	h2, .h2{
		font-size: 32px;
	}
	.ej-1__item-head{
		font-size: 20px;
		margin-bottom: 0;
	}
	.ej-1__item-text{
		font-size: 16px;
	}
	.top-line {
    padding: 20px 0;
		.logo{
			zoom: .7;
		}
		.home-header__social{
			zoom: .7;
		}
	}
	body{
		padding-top: 68px;
	}
	.main-banner {
		background-image: url(../images/dist/banner-bg-mob.png);
		background-size: 100%;
		padding-top: 105%;
		padding-bottom: 0;
	}
	.main-banner:before{
		display: none;
	}
	.h1, h1{
		font-size: 30px;
		span{
			font-size: 23px;
			display: block;
		}
	}
	.main-banner__content .h1{
		text-align: center;
	}
	.main-banner__content .h1:before{
		height: 7px;
		top: 20px;
		left: 50%;
		transform: translateX(-50%);
		width: 74%;
	}
	.main-banner__btn .button{
		min-width: 100%;
		max-width: 375px;
	}
	.ej-1{
		padding-top: 60px;
		padding-bottom: 30px;
	}
	.ej-2 .h2{
		margin-bottom: 20px;
	}
	.ej-1__inner{
		background-position: 100% 62%;
	}
	.slider-fleet .swiper-slide.swiper-slide-active .slider-fleet__item{
		max-width: 72%;
	}
	.slider-fleet__item-head{
		font-size: 20px;
	}
	.ej-contact{
		padding-top: 0;
	}
	.ej-contact .h2{
		margin-bottom: 60px;
	}
	.marker-wrapper{
		margin-bottom: 70px;
	}
	.main-banner__content .under-h1{
		text-align: center;
	}
}

