@font-face {
  font-family: "gothampro";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/GothamPro.woff2") format("woff2");
}
@font-face {
  font-family: "gothampro";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/GothamPro-Bold.woff2") format("woff2");
}
@font-face {
  font-family: "gothampro";
  font-style: normal;
  font-weight: 900;
  src: url("../fonts/GothamPro-Black.woff2") format("woff2");
}
@font-face {
  font-family: "hanson";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/Hanson-Bold.woff2") format("woff2");
}
::placeholder {
  color: #666;
}

::selection {
  background-color: #ffba00;
  color: #fff;
}

input, textarea {
  outline: none;
}
input:focus:required:invalid, textarea:focus:required:invalid {
  border-color: red;
}
input:required:valid, textarea:required:valid {
  border-color: green;
}

a, button {
  outline: none !important;
  text-decoration: none;
}

section {
  position: relative;
}

body {
  font-family: gothampro, sans-serif;
  font-size: 16px;
  line-height: 1.65;
  color: #000;
  min-width: 320px;
  position: relative;
  overflow-x: hidden;
  padding-top: 100px;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

h1, .h1 {
  font-size: 64px;
  letter-spacing: 0.005em;
  font-weight: 700;
  color: #151515;
}

h2, .h2 {
  font-size: 64px;
  letter-spacing: 0.005em;
  font-weight: 900;
  color: #151515;
  text-align: center;
}

.button {
  background-color: #ffba00;
  border-radius: 20px;
  padding: 20.5px 44px;
  border: 3px solid #ffba00;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  transition: all 0.25s ease;
}
.button:focus {
  color: #fff;
  background-color: #ffba00;
}
.button:hover {
  color: #000;
  background-color: #fff;
}

/* Button top */
.btn-top {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 30px;
  background-color: #ededed;
  box-shadow: 0px 4px 10px rgba(138, 138, 138, 0.4);
  border-radius: 12px;
  color: #fff;
  font-size: 26px;
  width: 50px;
  height: 50px;
  right: -100px;
  z-index: 15;
  cursor: pointer;
  text-align: center;
  transition: all 0.25s ease;
}
.btn-top.active {
  right: 30px;
}
.btn-top:hover {
  background-color: #ffba00;
  opacity: 1;
}

.cookie-notice {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem 0;
  bottom: 0;
  width: 100%;
  background: #191919;
  color: #F6F6F6;
  font-size: 0.9rem;
  z-index: 999;
}
.cookie-notice p {
  margin-bottom: 0;
}
.cookie-notice a {
  color: #F6F6F6;
}
.cookie-notice a:hover {
  text-decoration: none;
}
.cookie-notice a:visited {
  color: #F6F6F6;
}
.cookie-notice .cookie {
  font-size: 2.2rem;
  margin-right: 1rem;
  margin-left: 1rem;
}
.cookie-notice .accept-cookies {
  cursor: pointer;
  border: none;
  outline: none;
  background: #F6F6F6;
  color: #191919;
  font-weight: 700;
  padding: 0.5rem 1.5rem;
  margin-right: 1rem;
  margin-left: 3rem;
  transition: 0.35s ease;
}
.cookie-notice .accept-cookies:hover {
  box-shadow: 0.01rem 0.01rem 0.1rem #DDDDDD;
}

/** Header */
.home-header {
  box-shadow: 0px 4px 34px rgba(0, 0, 0, 0.15);
  position: fixed;
  width: 100%;
  z-index: 999;
  top: 0;
  background-color: #fff;
  opacity: 0;
  transform: translateY(-100%);
  transition: all 0.25s ease-in;
}
.home-header.is-show {
  opacity: 1;
  transform: translateY(0);
}
.home-header__nav {
  margin-right: 70px;
}
.home-header__nav-menu {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
}
.home-header__nav-menu li {
  margin: 0 15px;
}
.home-header__nav-menu li a {
  padding: 6px;
  font-size: 20px;
  text-decoration: none;
  color: #000;
  transition: color 0.25s ease;
}
.home-header__nav-menu li a:focus, .home-header__nav-menu li a:hover {
  color: #ffba00;
}
.home-header__social {
  display: flex;
  align-items: center;
}
.home-header__social-icon svg path {
  fill: #000;
  transition: fill 0.25s ease;
}
.home-header__social-icon:hover svg path {
  fill: #ffba00;
}
.home-header__social a {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  margin-right: 40px;
}
.home-header__social a:last-of-type {
  margin-right: 0;
}

.top-line {
  padding: 30px 0;
}
.top-line__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.top-line__inner-wrap {
  display: flex;
  align-items: center;
}

/** Main banner */
.main-banner {
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: right right;
  min-height: calc(100vh - 100px);
  min-height: 100vh;
  padding-top: 200px;
  padding-bottom: 200px;
  margin-bottom: 60px;
  opacity: 0;
  background-image: url(../images/dist/banner-bg.jpg);
  transition: all 0.35s ease-in;
}
.main-banner::before {
  content: "";
  position: absolute;
  right: 0;
  top: 23%;
  background-image: url(../images/dist/main-car.png);
  background-repeat: no-repeat;
  z-index: 2;
  width: 1165px;
  height: 639px;
  zoom: 0.87;
  opacity: 0;
  transform: translateX(100%);
  transition: all 0.3s ease-in;
}
.main-banner.is-show {
  opacity: 1;
}
.main-banner.is-show::before {
  opacity: 1;
  transform: translateX(0);
}
.main-banner.is-show .main-banner__content {
  opacity: 1;
  transform: translateY(0);
}
.main-banner__inner {
  position: relative;
  z-index: 5;
}
.main-banner__content {
  opacity: 0;
  transform: translateY(-50%);
  transition: all 0.3s ease-in;
}
