.slider-fleet .swiper-button-prev:after, .slider-fleet .swiper-rtl .swiper-button-next:after,
.slider-fleet .swiper-button-next:after, .slider-fleet .swiper-rtl .swiper-button-prev:after {
  display: none;
}
.slider-fleet .swiper-button-prev, .slider-fleet .swiper-rtl .swiper-button-next {
  left: 0;
}
.slider-fleet .swiper-button-next, .slider-fleet .swiper-rtl .swiper-button-prev {
